// src/Button.js
import React from 'react';
import { InputGroup, Row } from 'ff-core-components';
import { useState, useEffect } from 'react';
import InfoIcon from '@material-ui/icons/Info';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import Chip from '@material-ui/core/Chip';
import Checkbox from '@material-ui/core/Checkbox';
import AlertDialog from './common/AlertDialog';
import ReactDiffViewer, { DiffMethod } from 'react-diff-viewer';
//import { Button, Row, AppLoader, Column } from 'ff-core-components';

const useStyles = makeStyles((theme) => ({
    table: {
      minWidth: 650,
    },

    button: {
    marginLeft: theme.spacing(2),
    padding: '10px 16px', // Increased padding for size
    backgroundColor: '#4e51be',
    color: '#fff',
    border: 'none',
    borderRadius: '20px', // Makes the button round
    cursor: 'pointer',
    fontSize: '15px', // Optional: Adjust font size for a larger button
    fontWeight: 'bold', // Optional: Add bold text
    height: 'auto', // Ensures flexibility in height
},
  }));

const VariationOptions = ({
    allFirmwares,
    value,
    existingConfig,
    onRemove,
    index,
    updateFirmware,
    isExisting,
    isChecked
}) => {
    const classes = useStyles();
//    new code start
    const [comparisonData, setComparisonData] = useState(null);
    const [legacyLoading, setLegacyLoading] = useState(false);
    const [dialogContent, setDialogContent] = useState("");
//    new code end

    const getCompareData = async () => {
        // Mock API call to fetch comparison data
        return new Promise((resolve) => {
            setTimeout(() => resolve({ message: 'Comparison data fetched successfully' }), 1000);
        });
    };

    const compareChipsets = (display) => {
        setLegacyLoading(true);
        if (comparisonData) {
            setOpenComparison(display);
            setLegacyLoading(false);
            return;
        }
        getCompareData()
            .then((response) => {
                setComparisonData(response);
                setOpenComparison(display);
            })
            .catch((error) => {
                console.error('Error:', error);
                alert('Failed to fetch comparison data. Check the console for details.');
            })
            .finally(() => setLegacyLoading(false));
    };

    const preventDefault = (event) => event.preventDefault();
    const [ expand, setExpand ] = useState(false);
    const [ allBinaries, setAllBinaries ] = useState([]);
    const [ currentFirmwareDetails, setCurrentFirmwareDetails ] = useState(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [ selectedFirmware, setSelectedFirmware ] = useState(() => {
        if (value) {
            const foundItem = allFirmwares.find(item => item.version === value.version);
            return { label: value.version, value: value.version, friendlyName: foundItem ? foundItem.name : null }
        }
        return { label: '', value: '', friendlyName: null };
    });
    const [ firmwareChecked, setFirmwareChecked ] = useState(false);

    const [openDialog, setOpenDialog] = useState(false);
    const [openComparison, setOpenComparison] = useState(false);


    const clickInfo = () => {
        setExpand(!expand);
    }

    const handleDownloadManifest = (url) => {
        window.open(url, '_blank');
    };

    const handleAppDetails = (binary) => {
    alert(`Compare for ${binary}`);
    setOpenDialog(true);
    };

    const diffViewerStyles = {
        variables: {
            dark: {
                background: '#1e1e1e',
            },
            light: {
                background: '#fefefe',
            },
        },
        contentText: {
            fontSize: '15px', // Increased font size for better readability
            lineHeight: '2.5', // Adjusted line height for more spacing
        },
        gutter: {
            padding: '0 15px', // Increased padding to make the gutter more prominent
        },
        title: {
        fontWeight: 'bold', // Make the titles bold
        textAlign: 'center', // Center-align the titles
        width: '100%', // Ensure the width takes the full available space
        display: 'flex', // Use flexbox to center the titles
        justifyContent: 'center', // Horizontally center
        alignItems: 'center', // Vertically center
    },
    };


    const prod = {
        APPS: {
            Version: "58.35",
            LocalApps: "https://scfs2.vizio.com/localassets/drop-prod-canary-240822-fjgndfg.enc",
            BinaryManifest: "https://scfs2.vizio.com/app/[SOC]/[PLATFORM VER]/manifest_prod.enc",
            AcrBlacklistUri: "https://scfs2.vizio.com/blacklist/blacklist-240702.enc",
            url: "https://cdn.lokistore.vzolab.com/v1",
            VIZIOgram: { api: "https://api.viziogram-prod.smartcasttv.com/v1", webapp: "https://loki.viziogram-prod.smartcasttv.com" },
            VIZIOSDK: { URL: "https://example.com/xyz-prod", VERSION: "1.0.9" },
            VIZIOWEBPLAYER: { URL: "https://example.com/xyz-prod", VERSION: "1.1.1" },
        },
    };

    const canary = {
        APPS: {
            Version: "58.53",
            LocalApps: "https://scfs2.vizio.com/localassets/drop-prod-canary-240822-76366.enc",
            BinaryManifest: "https://scfs2.vizio.com/app/[SOC]/[PLATFORM VER]/manifest_canary.enc",
            AcrBlacklistUri: "https://scfs2.vizio.com/blacklist/blacklist-240702.enc",
            url: "https://cdn.lokistore.vzolab.com/v1",
            VIZIOgram: { api: "https://api.viziogram-canary.smartcasttv.com/v1", webapp: "https://loki.viziogram-canary.smartcasttv.com" },
            VIZIOSDK: { URL: "https://example.com/xyz-canary", VERSION: "1.0.7" },
            VIZIOWEBPLAYER: { URL: "https://example.com/xyz-canary", VERSION: "1.0.4" },
        },
    };

    const handleCompareClick = () => {
        setOpenComparison(true);
    };


    useEffect(() => {
        if(value) {
            const foundItem = allFirmwares.find(item => item.version === value.version);
            if (foundItem) {
                setCurrentFirmwareDetails(foundItem);
                const mergedKeys = new Set([
                    ...Object.keys(foundItem.manifest.prod.APPS ? foundItem.manifest.prod.APPS : []),
                    ...Object.keys(foundItem.manifest.canary.APPS ? foundItem.manifest.canary.APPS : [])
                ]);
                const distinctKeys = Array.from(new Set(mergedKeys));;
                setAllBinaries(distinctKeys);
            }
            setFirmwareChecked(value.isChecked);
            setSelectedFirmware({ label: value.version, value: value.version, friendlyName: foundItem ? foundItem.name : null })
        }
    }, [value])
    let filteredFirmware = [];
    if (existingConfig && existingConfig.length > 0) {
        const existingConfigFirmwares = existingConfig.map(item => item.version);
        filteredFirmware = allFirmwares.filter(firmware => !existingConfigFirmwares.includes(firmware.version));
    } else {
        filteredFirmware = allFirmwares;
    }

    const transformedFirmwares = filteredFirmware.map(firmware => ({ label: firmware.version + '  (' + firmware.name + ')', value: firmware.version, friendlyName: firmware.name }));

    const options = {
        day: 'numeric',
        month: 'short',
        year: 'numeric',
        hour: 'numeric',
        minute: '2-digit',
        hour12: true,
        timeZoneName: 'short'
    };

    const handleCloseDialog = () => {
    setOpenDialog(false);
    setOpenComparison(false);
  };

    return (
        <>
        <Row style={{ alignItems: 'center' }}>
            <Checkbox
                checked={firmwareChecked}
                color="primary"
                onChange={(e) => {
                    setFirmwareChecked(e.target.checked)
                    updateFirmware(index, {
                        ...value,
                        isChecked: e.target.checked
                    })
                }}
            />
            <div className='flex flex-1'>
                <InputGroup
                    component={
                        <>
                        <Select
                            value={selectedFirmware}
                            placeholder={'Select Firmware'}
                            onChange={(v) => {
                                setSelectedFirmware(v);
                                updateFirmware(index, {
                                    ...value,
                                    version: v.value,
                                    isDirty: true
                                })
                            }}
                            options={transformedFirmwares}
                            isDisabled={value ? value.isExisting : false}
                            styles={{
                                control: (base) => ({
                                ...base,
                                background: 'hsl(0,0%,100%)',
                                }),
                            }}
                        />
                        </>
                    }
                    title={selectedFirmware && selectedFirmware.friendlyName ? selectedFirmware.friendlyName : 'Firmware'}
                    inputProps={{
                        style: { marginBottom: 15 },
                    }}
                />
            </div>
            <div className='variation-weight ml-2' style={{ width: 210 }}>
                <InputGroup
                    type='text'
                    onChange={(e) => {
                        updateFirmware(index, {
                            ...value,
                            percent: e.target.value ? parseInt(e.target.value) : 0,
                            isDirty: true
                        })
                    }}
                    value={value ? value.percent : ''}
                    inputProps={{
                        maxLength: 3,
                        style: { marginTop: 2, maxHeight: 40 },
                    }}
                    title={'Rollout Percentage'}
                />
            </div>
            {
                currentFirmwareDetails &&
                <span>
                    <InfoIcon fontSize="large" color="primary" onClick={() => clickInfo()} style={{ cursor: 'pointer' }}/>
                </span>
            }
            {
                (!value || !value.isExisting) && (existingConfig && existingConfig.length > 1) ?
                    <div className='ml-2' style={{ width: 30 }}>
                        <button
                            onClick={() => onRemove(index)}
                            id='delete-multivariate'
                            type='button'
                            className='btn btn--with-icon ml-auto btn--remove'
                            style={{ position: 'relative' }}
                        >
                            <RemoveIcon />
                        </button>
                    </div> : null
            }
        </Row>
        {
            isExisting && <Row>
                <div className='flex flex-1'>
                    <p style={{ marginLeft: '42px', marginBottom: '40px', fontStyle: 'italic' }}><b>Total TVs:</b> {value.statistics.total}&nbsp;&nbsp;&nbsp;&nbsp;<b>Adoption:</b> ({ value.statistics.total > 0 ? ((value.statistics.actual / value.statistics.total) * 100).toFixed(1) : '0.0' }%) &nbsp;&nbsp;{
                        value.statistics.refreshed_on ? `As of ${new Date(value.statistics.refreshed_on * 1000).toLocaleString(undefined, options)}` : ''
                    }</p>
                </div>
            </Row>
        }
        {
            currentFirmwareDetails && expand ? (
                <Row style={{ paddingBottom: '20px' }}>
                    <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>Binary</TableCell>
                            <TableCell>Prod</TableCell>
                            <TableCell>Canary</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {allBinaries.map((binary) => (
                            <TableRow key={binary}>
                            <TableCell component="th" scope="row">
                                {binary}
                            </TableCell>
                            <TableCell>
                                {currentFirmwareDetails.manifest.prod.APPS[binary]?.VERSION && (
                                  <Link
                                    href="#"
                                    onClick={() =>
                                      handleDownloadManifest(
                                        currentFirmwareDetails.manifest.prod.APPS[binary].URL
                                      )
                                    }
                                  >
                                    {currentFirmwareDetails.manifest.prod.APPS[binary].VERSION}
                                  </Link>
                                )}
                                </TableCell>
                                <TableCell>
                                {
                              currentFirmwareDetails.manifest.canary.APPS[binary] && currentFirmwareDetails.manifest.prod.APPS[binary] ? (
                                currentFirmwareDetails.manifest.prod.APPS[binary].VERSION !== currentFirmwareDetails.manifest.canary.APPS[binary].VERSION ? (
                                  <Chip
                                    label={currentFirmwareDetails.manifest.canary.APPS[binary].VERSION}
                                    color="secondary"
                                    onClick={() => handleDownloadManifest(currentFirmwareDetails.manifest.canary.APPS[binary].URL)}
                                  />
                                ) : (
                                  <Link
                                    href="#"
                                    onClick={() => handleDownloadManifest(currentFirmwareDetails.manifest.canary.APPS[binary].URL)}
                                  >
                                    {currentFirmwareDetails.manifest.canary.APPS[binary].VERSION}
                                  </Link>
                                )
                              ) : (
                                <Link href="#">
                                  {currentFirmwareDetails.manifest.canary.APPS[binary]?.VERSION}
                                </Link>
                              )
                            }
                            {binary === 'SCCONFIG' && (
                            <button
                              className={classes.button}
                              onClick={handleCompareClick}
                              style={{
                                marginLeft: "80px",
                                cursor: "pointer", // Ensure pointer cursor is visible
                                display: "inline-block", // Ensure button layout is consistent
                              }}
                            >
                              Compare
                            </button>
                            )}
                            </TableCell>
                            </TableRow>
                        ))}
                        </TableBody>
                    </Table>
                    </TableContainer>
                </Row>
            ) : null
        }

        {openComparison && (
                <AlertDialog
                    title=""
                    text={
                        <ReactDiffViewer
                            oldValue={JSON.stringify(prod, null, 2)}
                            newValue={JSON.stringify(canary, null, 2)}
                            compareMethod={DiffMethod.WORDS}
                            showDiffOnly={true}
                            extraLinesSurroundingDiff={0}
                            splitView={true}
                            styles={diffViewerStyles}
                            codeFoldMessageRenderer={() => null}
                            leftTitle={<span style={{ fontWeight: 'bold', textAlign: 'center' }}>Prod</span>}
                            rightTitle={<span style={{ fontWeight: 'bold', textAlign: 'center' }}>Canary</span>}
                        />
                    }
                    isOpen={openComparison}
                    handleClose={handleCloseDialog}
                    fullScreen={true}
                    showActions={false}
                />
            )}
        </>
    );
};

export default VariationOptions;