import React, { useState, useEffect } from 'react';
import Alert from '@material-ui/lab/Alert';
import Collapse from '@material-ui/core/Collapse';

const AutoCloseAlert = ({ type, message, setApiError, ...props }) => {
  const [open, setOpen] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setOpen(false);
      setApiError(null);
    }, 10000); // Auto close after 5 seconds

    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <Collapse in={open} {...props} >
      <Alert variant={props.variant} severity={type} onClose={() => setOpen(false)}>
        {message}
      </Alert>
    </Collapse>
  );
};

export default AutoCloseAlert;
